<script>
import { tick } from 'svelte';
import { getApp, setApp, getAPIInfo, setAPIInfo, getDEMOImages, setDEMOImages } from '../utils/config.js';
import { APPS } from '../utils/constant.js';

export let pxl;

let appname = getApp();
let { apibase, apikey } = getAPIInfo();
let demoImages = getDEMOImages();

let isSettingOpen = false;
let modal;
let demoImagesInput = '';
let magicLink;
$: {
  setApp(appname);
};
$: {
  setAPIInfo({
    apibase: apibase,
    apikey: apikey,
  }, pxl);
};
$: isJSONMalform = demoImagesInput.length > 0 && !isValidJSON(demoImagesInput);
$: {
  if (!isJSONMalform) {
    demoImages = demoImagesInput;
    setDEMOImages(demoImagesInput);
  }
};
$: magicLink = `${window.location.origin}/?base=${encodeURIComponent(apibase)}&key=${encodeURIComponent(apikey)}`;

function dimmerClick(event) {
  if (modal.contains(event.target)) {
    return;
  }
  closeModal();
}

function openModal() {
  isSettingOpen = true;
}

function closeModal() {
  isSettingOpen = false;
}

async function updateAPIBase() {
  apibase = APPS[appname].apibase;
}

function isValidJSON(str) {
  try {
    JSON.parse(str);
  } catch(e) {
    return false;
  }
  const json = JSON.parse(str);
  return Array.isArray(json) && json.length > 0;
}

function bodyKeyup(event) {
  if (isSettingOpen && event.keyCode === 27) {
    closeModal();
  }
}
</script>

<svelte:body on:keyup={bodyKeyup} />
<button id="setting-button" on:click|preventDefault={openModal}><i class="fas fa-cog"></i></button>
<div id="api-setting" class:on={isSettingOpen} on:click={dimmerClick}>
  <form class="block shadow-sm text-align-left" on:submit|preventDefault={closeModal} bind:this={modal}>
    <div class="form-item">
      App
      <div class="label-group">
        {#each Object.keys(APPS) as item}
        <label>
          <input type="radio" name="app" bind:group={appname} value={item} on:change={updateAPIBase}>
          {APPS[item].name}
        </label>
        {/each}
      </div>
    </div>
    <label>
      API base
      <input type="text" bind:value={apibase}>
    </label>
    <label>
      API key
      <input type="text" id="input_apikey" bind:value={apikey}>
    </label>
    <label>
      Demo images
      <textarea bind:value={demoImagesInput} class:err={isJSONMalform} placeholder="[&quot;imageURL&quot;, &quot;imageURL&quot;, ... ]"></textarea>
    </label>
    <p class="magic-link">Magic link: {magicLink}</p>
    <input type="submit" value="Apply">
  </form>
</div>

